import { IValue } from './ivalue';
import { ControlType } from '../controltype';
import { EventEmitter } from '@angular/core';
import { DateTimeHelperService } from '@farris/ui-common/date';
export class YearValue implements IValue {
    value: string;
    clear$ = new EventEmitter();
    constructor(value: { value: string } = { value: '' }) {
        this.value = value.value;
    }

    setOriginalValue(value: any): void {
        this.value = value.formatted;
    }
    getOriginalValue() {
        return this.value;
    }
    getControlType(): ControlType {
        return ControlType.Date;
    }

    isEmpty(): boolean {
        return !this.value;
    }
    clearValue(): void {
        this.value = undefined;
        this.clear$.emit();
    }
    getValueText(ctrl?: any): string {
        if (ctrl && ctrl.format) {
            return new DateTimeHelperService().formatTo(this.value, ctrl.format);
        }

        return this.value;
    }
}